<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      loginStatus: false,
      role: '',
      deleteEventId: ''
    }
  },
  methods: {
    checkAuth: function () {
        if(this.getCookie('role') == undefined 
        && this.getCookie('id') == undefined
        && this.getCookie('hash') == undefined){
            this.$router.push({ path: '/'});
        }
    },
    logout: function () {
      var self = this;
      fetch(AUTH_URL, {
          method: 'delete',
          headers: {  
              "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
          },  
          credentials: "include"
      })
      .then(self.status)
      .then(self.json)
      .then(function (data) {
          if(data == true){
            self.$router.push({ path: '/'});
          }
      });
    },
    getCookie: function (name) {
      var matches = document.cookie.match(new RegExp(
          "(?:^|; )" + name.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, '\\$1') + "=([^;]*)"
      ));
      return matches ? decodeURIComponent(matches[1]) : undefined;
    },
    status: function (response) { 
      if (response.status >= 200 && response.status < 300) {  
          return Promise.resolve(response)  
      } else {  
          return Promise.reject(new Error(response.statusText))  
      }  
    },
    json: function(response,self) {
        return response.json()  
    },
  }

}
</script>

<style>

.home {
  margin-left: 20px;
  margin-top: 20px;
}

.logout{
  float: right;
   margin-right: 20px;
  margin-top: 20px;
}
</style>



